import React,{Component} from 'react';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.bubble.css';
import axios from 'axios';
import {Row,Col} from 'react-bootstrap';
import { Button } from 'reactstrap';
import moment from 'moment';
class CreateEntry extends Component{
  constructor(props){
    super(props);
    this.state = {
      journal_date : moment().format('ll'),
      title : "",
      best_moment : "",
      health_message : "",
      family_message : "",
      future_message : "",
      tmrw_message : "",
      reaction : ""
   }
    this.titleChange = this.titleChange.bind(this);
    this.best_moment_Change = this.best_moment_Change.bind(this);
    this.health_msg_Change = this.health_msg_Change.bind(this);
    this.family_msg_Change = this.family_msg_Change.bind(this);
    this.future_msg_Change = this.future_msg_Change.bind(this);
    this.tmrw_msg_Change = this.tmrw_msg_Change.bind(this);
    this.fnSaveEntry = this.fnSaveEntry.bind(this);
  }
  titleChange(value) {

    this.setState({ title: value })
  }
  best_moment_Change(value){
    this.setState({ best_moment: value })
  }
  family_msg_Change(value){
    this.setState({ family_message: value })
  }
  health_msg_Change(value){
    this.setState({ health_message: value })
  }

  future_msg_Change(value){
    this.setState({ future_message: value })
  }
  tmrw_msg_Change(value){
    this.setState({ tmrw_message: value })
  }
  componentDidMount(){

  }
  fnSaveEntry(){
    const{className} = this.props.location.state;
    const {journal_date, title, best_moment ,health_message,family_message,future_message ,
    tmrw_message,reaction} = this.state;
    axios.post('/notes', { journal_date, title, best_moment ,health_message,family_message,future_message ,
    tmrw_message,reaction,className })
     .then(result => {
       this.props.history.push("/")
     });
  }
  render(){
    return (
      <div className="container-fluid create-entry">
      <Row>
        <Col xs={12} sm={6} md={4} lg={4}>
        <ReactQuill className="title-editor" theme="bubble" placeholder="Every Single day is UNIQUE, Describe ur day in 3 words or less!" value={this.state.title || " "}
                  onChange={this.titleChange} >
        </ReactQuill>
      </Col>
      <Col  xs={12} sm={6} md={4} lg={4}>
        <ReactQuill className="best-moment-editor" theme="bubble" placeholder="Best moment of the day?" value={this.state.best_moment}
                  onChange={this.best_moment_Change} />
      </Col>
      <Col xs={12} sm={6} md={4} lg={4}>
      <ReactQuill className="health-editor" theme="bubble" placeholder="What did I do today to make my health better?" value={this.state.health_message || " "}
                onChange={this.health_msg_Change} >
      </ReactQuill>
    </Col>
    <Col  xs={12} sm={6} md={4} lg={4}>
      <ReactQuill className="family-editor" theme="bubble" placeholder="Throwback to one memory with Sourabh/Shivani" value={this.state.family_message}
                onChange={this.family_msg_Change} />
    </Col>
    <Col  xs={12} sm={6} md={4} lg={4}>
      <ReactQuill className="future-editor" theme="bubble" placeholder="What did I do today to make my future better?" value={this.state.future_message}
                onChange={this.future_msg_Change} />
    </Col>
    <Col  xs={12} sm={6} md={4} lg={4}>
      <ReactQuill className="tmrw-editor" theme="bubble" placeholder="One thing I want to do better tomorrow?" value={this.state.tmrw_message}
                onChange={this.tmrw_msg_Change} />
    </Col>

      </Row>
      <Row>



      </Row>
        <Button className="new-note-save-btn" color="success" onClick={this.fnSaveEntry}>Save</Button>
      </div>
    );
  }
}
export default CreateEntry;
